
import tools from '../../lib/tools.js';
import tabBar from '../../components/TabBar.vue';
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'personalTailorIndex',
components:{tabBar,Toast},
directives: {TransferDom},
data(){
return {
params:{},
showToast:false,
toastMsg:'',
info:{},
codeText:'获取验证码',
submitName:'',
submitPhone:'',
submitCode:'',
num:59,
isDisabled:false,
showTabBar:true
}
},
mounted(){
let params = tools.getRequestParam();
this.params = params;
let vm = this;
this.$http.axios({method:'POST',url:'/personalTailor/queryByEntity',data:{id:params.id,applyType:params.applyType,currentPage:1,showCount:10}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.info = data.items[0];
document.getElementById('tailorText').innerHTML = vm.info.richText;
const imgs = document.querySelectorAll('#tailorText img');
for(var i=0;i<imgs.length;i++){
imgs[i].style.display = 'block';
imgs[i].style.maxWidth = '100%';
imgs[i].style.height = 'auto';
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
}
},(error) => {
console.error(error);
})
},
methods:{
submitCodeFn(){
let vm = this;
if(this.isDisabled){
return ;
}
if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.submitPhone))){
this.showToast = true;
this.toastMsg = '手机号码格式错误';
return ;
}

this.$http.post('/ptmessage/sendVerifyCode',{mobile:this.submitPhone}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
if(data.resultCode.code!='CODE_SENT'){
vm.codeText = '重新获取60s';
vm.isDisabled = true;
let timer = setInterval(() => {
if(vm.num==0){
vm.codeText = '获取验证码';
vm.isDisabled = false;
clearInterval(timer);
return ;
}
vm.codeText = '重新获取'+vm.num+'s';
vm.num--;
},1000)
}
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
vm.isDisabled = false;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '获取失败';
vm.isDisabled = false;
})

},
saveTailorInfo(){
if(!this.submitPhone || !(/^1(3|4|5|7|8)\d{9}$/.test(this.submitPhone))){
this.showToast = true;
this.toastMsg = '手机号码格式错误';
return ;
}
if(!this.submitCode || this.submitCode.length!=6){
this.showToast = true;
this.toastMsg = '请正确输入验证码';
return ;
}
if(!(this.submitName || this.submitPhone || this.submitCode)){
this.showToast = true;
this.toastMsg = '请将申请信息填写完整';
return false;
}
let vm = this;
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id')); 
let data = {
applyType:this.info.applyType,
personalId:this.params.id,
name:this.submitName,
mobile:this.submitPhone
}
if(managerId && managerId!='undefined'){
data.managerId = managerId;
}
this.$http.axios({method:'POST',url:'/ptmessage/add?verifyCode='+this.submitCode,data:data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
if(data.resultCode.code=='SUCCESS'){
setTimeout(() => {
tools.next('/m/personalTailor.html');
},800)
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
}
},(error) => {
console.error(error);
})
}
}
}



